video {
	position: fixed;
	right: 0;
	bottom: 0;
	min-height: 100%;
	min-width: 100%;
	z-index: 0;
}

/* The Close Button */
.close {
	color: #1b3044;
	float: left;
	font-size: 60px;
	font-weight: bold;
	position: absolute;
	top: 20px;
	left: 20px;
	z-index: 2;
}

.close:hover,
.close:focus {
	color: black;
	text-decoration: none;
	cursor: pointer;
}

.qrcode {
	position: fixed;
	top: 0px;
	width: 100vw;
	height: 100vh;
	z-index: 1;
}

.qrcode section {
	width: 100vw !important;
	height: 100vh !important;
	padding-top: 0 !important;
}

.qrcode section div {
	width: 80vw !important;
	height: 35vw !important;
	margin: 40vh 10vw;
	border: 2px dashed #fff !important;
	box-shadow: inset 0 0 0 0 transparent !important;
	border-radius: 10px;
}

.qrcode section video {
	height: 100vh !important;
}

.barcode-popup {
	height: 50px;
	width: 50vw;
	position: fixed;
	bottom: 100px;
	background-color: white;
	z-index: 10;
	border-radius: 20px;
	left: 25vw;
	text-align: center;
	padding: 20px;
}
